<template>
  <div class="footer">    
      <div class="shopcarts">
          <div class="shopwrap">
               <div class="graywrap" :class="{'lightblue':selectFoods.length >= 1}">
                   <i class="iconfont icon-gouwuche" @click="showcartlist"
                   :class="{'white':selectFoods.length >= 1}"></i>
               </div>
          </div>
          <span class="tips" v-show="selectFoods.length">{{totalcount}}</span>   
          <div class='cartinfo'>
            <p :class="{'white':selectFoods.length>= 1}">￥{{totalprice}}</p>
            <p>另需配送费￥{{seller.deliveryPrice}}元</p>
            <p>{{pay}}</p>
          </div>
      </div>  
      <shopcart :selectFoods="selectFoods"  v-show= 'cartshow'></shopcart>    
  </div>
</template>
<script>
    import { mapGetters , mapActions} from 'vuex';  
    import shopcart from '../shopcart/shopcart.vue';
    export default {
        props:['goods'],
        components:{
            shopcart
        },
        methods:{
            ...mapActions([
                'showcartlist'
            ])
        },
        computed:{
            ...mapGetters([
                'seller', 
                'cartshow'  
                                
            ]),
            selectFoods(){
                let foods = [];
                this.goods.forEach((good)=>{
                    good.foods.forEach((food)=>{
                        if(food.count > 0){
                            foods.push(food);
                        }
                    })
                });
                return foods;
            },
            totalcount(){
                let count = 0;
                this.selectFoods.forEach((food=>{
                    count += food.count;
                }));
                return count;
            },
            totalprice(){
                let price=0;
                this.selectFoods.forEach((food=>{
                    price += food.count * food.price;
                }))
                return price;
            },
            pay(){
                if(this.totalprice===0){
                    return `￥${this.seller.minPrice}起送`;
                }else if (this.totalprice < this.seller.minPrice){
                    return `还差${this.seller.minPrice - this.totalprice  }配送`
                }else{
                    return `去结算`
                }
            }
        },
    }
</script>
<style scoped lang="less">
    .footer{
        position: fixed;
        left: 0;
        bottom:0;
        height: 0.9rem;
        width: 100%;     
        .shopcarts{
            height: 0.9rem;
            background-color:#141d27; 
            font-size: 0;
            .shopwrap{
                position: absolute;
                top:-0.2rem;
                width: 1.12rem;
                height: 1.12rem;
                border-radius: 50%;
                margin-left: 0.24rem;
                background-color:#141d27;
                .graywrap{
                    width: 0.88rem;
                    height: 0.88rem;
                    background-color: #2b343c;
                    font-size: 0.48rem;
                    line-height: 0.48rem;
                    border-radius:50%;
                    margin: 0.12rem;
                    display: table;
                    &.lightblue{
                        background-color: rgb(0,160,220);
                    }
                    i{
                        display: table-cell;
                        text-align: center;
                        vertical-align: middle;
                        color: rgba(255,255,255,0.4);
                        line-height: 0.48rem;
                        font-size: 0.48rem;
                        &.white{
                           color: rgb(255,255,255);
                        }
                    }
                }
            }   
            .tips{
                display: block;              
                background-color: rgb(240,20,20);
                box-shadow: 0 4px 8px 0px rgba(0,0,0,0.4);
                font-size: 0.18rem;
                font-weight: 700;
                color: rgb(255,255,2555);
                width:0.48rem;
                line-height: 0.3rem;
                height: 0.3rem;
                border-radius: 0.16rem;
                padding-top: 0.02rem;
                text-align: center;
                position: absolute;
                top:-0.21rem;
                left: 0.88rem;
            }
            .cartinfo{
                margin-left:1.6rem;
                height: 0.9rem; 
                overflow: hidden;            
                p{
                    color: rgba(255,255,255,0.4);
                    font-size: 0.32rem;               
                    line-height: 0.42rem;
                    height: 0.42rem;
                    display: inline-block;
                }  
                p:first-child{
                    font-weight: 700;  
                    margin: 0.24rem 0;               
                    border-right: 1px solid rgba(255,255,255,0.1);
                    padding-right: 0.24rem;
                    &.white{
                        color:rgb(255,255,255);
                    }
                }
                p:nth-child(2){                 
                    font-size: 0.2rem;  
                    font-weight: normal;             
                    padding: 0 0.24rem;
                }
                p:last-child{
                    height: 0.9rem;
                    line-height: 0.9rem;
                    font-size: 0.24rem;
                    width: 2.1rem;
                    float:right;
                    text-align: center;
                    padding:0 0.16rem;
                    background-color: #2b343c;
                }

            }     
            
        }
    }
</style>





