<template>
  <div class="ratings">
    <div class="score">
        <div class="score-left">
            <ul>
                <li>{{seller.score}}</li>
                <li>
                    <p>综合评分</p>
                </li>
                <li>
                    <p>高于周边商家{{seller.rankRate}}%</p>
                </li>
            </ul>
        </div>
        <div class="score-right">
            <ul>
                <li>
                    <p>服务态度</p>
                    <star :score = 'seller.serviceScore'></star>
                    <span>{{seller.serviceScore}}</span>
                </li>
                <li>
                    <p>食品评价</p>
                    <star :score = 'seller.foodScore'></star>
                    <span>{{seller.foodScore}}</span>
                </li>
                <li>
                    <p>送达时间</p>
                    <span>{{seller.deliveryTime}}分钟送达</span>
                </li>
            </ul>            
        </div>
    </div>
    <div class="sliceline"></div> 
    <ratingslist></ratingslist>   
    <div  class="ratinglists" ref="rate"  >
        <ul > 
            <li  v-for="(rating,i) in selectRatings" :key="i"  class="ratingitem">              
                <img :src="rating.avatar" alt="">
                <div class='ratinginfos'>
                    <p>
                        <span>{{rating.username}}</span>
                        <span>{{rating.rateTime | data-format }}</span>
                    </p>              
                    <p>
                         <star :score = 'rating.score' ></star>
                         <span v-if="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
                    </p>                       
                    <p>{{rating.text}}</p>
                    <div class="recommend">
                        <i :class="rating.rateType ? 'iconfont icon-zan2' : 'iconfont icon-dianzan_active' "></i>
                        <ul>
                            <li v-for="(item ,i) in rating.recommend" :key="i" >
                                {{item}}
                            </li>
                        </ul>
                    </div>
                </div>
            </li>          
        </ul>
    </div>

  </div>
</template>
<script>
    import BScroll from 'better-scroll';   
    import { mapGetters , mapActions} from 'vuex';   
    import ratingslist from '../ratingslist/ratingslist.vue';
    import star from '../star/star.vue';
    export default {
        created(){
            this.$nextTick( () => {            
                this.$store.dispatch('getRatings');
            })
        },
        computed:{
            ...mapGetters([
                'seller',
                'ratings',
                'types' ,
                'selectRatings',
             ]),  
        },
        components:{
            ratingslist,
            star,
        },
        filters:{
           "data-format":function(data){  
                var da = new Date(data);           
                var year = da.getFullYear();
                var month = da.getMonth()+1;
                var date = da.getDate();
                var hour = da.getHours();
                var minutes = da.getSeconds();                                                 
                return year+"-"+(month<10?"0"+month:month)+"-"+date+" "+(hour<10?"0"+hour:hour)+":"+(minutes<10?"0"+minutes:minutes);
            }
        },
        watch:{
            selectRatings(){        
                this.$nextTick( () => {
                    setTimeout( () => {
                        if( !this.rateBScroll ){                     
                            this.rateBScroll = new BScroll( this.$refs.rate , {
                                click : true
                            });
                        }else{
                            this.rateBScroll.refresh();                  
                        }
                    },30)
                })
            }
        },    
    }
</script>
<style scoped lang="less" >
    .ratings{
        position: absolute;
        top:3.5rem;
        overflow: hidden;
        width: 100%;
        .score{
            padding: 0.36rem 0;
            width: 100%;
            display: table;
            .score-left{
                display: table-cell;
                width: 2.75rem;
                text-align: center;                
                vertical-align: middle;
                border-right: 1px solid rgba(7, 17, 27, 0.1);
                li:first-child{
                    font-size: 0.48rem;
                    color: rgb(255,153,0);
                    line-height: 0.56rem;
                    margin-bottom: 0.12rem;                  
                }
                li:nth-child(2){
                    font-size: 0.24rem;
                    color: rgb(7,17,27);
                    line-height: 0.24rem;
                    margin-bottom: 0.16rem;
                }
                li:last-child{
                    font-size: 0.2rem;
                    color:rgb(147,153,159);
                    margin-bottom: 0.12rem;
                }
            }
            .score-right{
                display: table-cell;
                width: 4.74rem;
                padding: 0 0.48rem;
                li{
                   display: flex;
                   margin-bottom:0.16rem;
                    span{
                        font-size: 0.24rem;
                        color: rgb(255,153,0);
                        line-height: 0.36rem;
                    }
                   p{
                       font-size: 0.24rem;
                       color: rgb(7,17,27);
                       line-height: 0.36rem;
                       margin-right: 0.24rem;
                   }
                }
                li:last-child{
                    margin: 0;
                    span{
                        font-size: 0.24rem;
                        color: rgb(147,153,159);
                        line-height: 0.36rem;
                    }
                }
            }
        }
        .sliceline{
            padding-top: 0.32rem;
            background-color: #f3f5f7;
            border-top: 1px solid rgba(7, 17, 27, 0.1);
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);            
        }
        .ratinglists{
            height:5rem;
            overflow:hidden;
            ul{
                 padding:0 0.36rem;
                .ratingitem{
                   border-bottom: 1px solid rgba(7, 17, 27, 0.1);    
                   padding:0.36rem 0 0.28rem 0;  
                   font-size: 0;                  
                    img{
                        display: inline-block;
                        width: 0.56rem;
                        height: 0.56rem;
                        border-radius: 50%;
                        vertical-align: top;
                    }
                    .ratinginfos{
                        display: inline-block;
                        width: 5.78rem;
                        margin-left: 0.24rem;
                        font-size: 0.24rem;
                     
                        p:first-child{
                            display: flex;
                            justify-content: space-between;
                            span:first-child{
                                font-size: 0.2rem;                              
                                color: rgb(7,17,27);
                                line-height: 0.24rem;
                            }
                            span:last-child{
                                font-size: 0.2rem;
                                font-weight: 200;
                                color: rgb(147,153,159);
                                line-height: 0.24rem;
                            }
                        }
                        p:nth-child(2){
                            display: table;
                            margin: 0.08rem 0 0.12rem 0;
                            span{
                                padding-left: 0.12rem;
                                display: table-cell;
                                font-size: 0.2rem;
                                padding-top: 0.04rem;
                                font-weight: 200;
                                height: 0.24rem;
                                vertical-align: middle;
                                color: rgb(147,153,159);
                                line-height: 0.24rem;
                            }
                        }
                        p:nth-child(3){
                            font-size: 0.24rem;
                            color:rgb(7,17,27);
                            line-height: 0.36rem;
                            margin-bottom: 0.16rem;
                        }
                        .recommend{
                            display: flex;                     
                            i{
                                line-height: 0.32rem;
                                color:rgb(0,160,220);
                                &.icon-zan2{
                                    font-size: 0.2rem;
                                    color:rgb(183,187,191);                                   
                                }
                            }
                            ul{                          
                                margin-left: 0.16rem;
                                padding: 0;     
                                font-size: 0;  
                                line-height: 0.32rem;                   
                                li{    
                                    display: inline-block;                                                             
                                    text-align: center;
                                    margin-right: 0.16rem;
                                    padding: 0 0.12rem;
                                    border: 1px solid rgba(7, 17, 27, 0.1);
                                    border-radius: 0.02rem;
                                    font-size: 0.18rem;
                                    color:rgb(147,153,159);
                                    line-height: 0.32rem;
                                }
                                li:last-child{
                                    margin: 0;
                                }
                            }
                        }
                        
                    }
                        
                }
               

            }
        }
      
    }
</style>



