<template>
  <div class="ratingslist">
    <div class="countlist">
          <ul ref="selects">
              <li class="select-list-hook">
                <span  @click="select(2)" >{{types[2]}}</span>
                <span >{{ratings.length}}</span>   
              </li>
              <li  class="select-list-hook">
                 <span @click="select(0)" >{{types[0]}}</span>
                 <span>{{likecount}}</span>  
              </li>
              <li class="select-list-hook">
                  <span  @click="select(1)" >{{types[1]}}</span>
                  <span>{{discount}}</span>  
              </li>
          </ul>
      </div>
      <div class="only" @click="toggle">
          <i class="iconfont icon-duigou" :class=" {'_blue': logchange===true} "></i>
          <span>只看有内容的评价</span>
      </div>
  </div>
</template>
<script>
    import { mapGetters , mapActions} from 'vuex'; 

    
    export default {
        computed:{
            ...mapGetters([              
                'types' ,
                'ratings',
                'logchange'
            ]),  
            likecount(){
                return this.ratings.filter( rating => {
                    return rating.rateType === 0
                }).length
            },
            discount(){
                return this.ratings.filter( rating => {
                    return rating.rateType === 1
                }).length
            }
        },   
        created(){
            this.$nextTick( () => {
                setTimeout( () => {
                    this.$store.dispatch( "changeTypes" , ['推荐','吐槽','全部'] )
                },300)
            })
        },
        methods:{
            select(typenumber){
                this.$store.dispatch( "select" , typenumber );
                let lis = this.$refs.selects.getElementsByClassName('select-list-hook');
                for( let i=0 ; i<lis.length;i++){               
                    lis[i].style.color='rgb(77,85,93)';
                }
                event.target.parentNode.style.color='#fff'   ;
                console.log(event)       
            },
            toggle(){
                this.$store.dispatch( "toggle" )                
            },
           
        }
      
    }
  
</script>
<style scoped lang="less" >
    .ratingslist{
        .countlist{
            padding-top: 0.36rem;        
            ul{
                margin:0 0.36rem;
                padding-bottom: 0.36rem;
                border-bottom:1px solid rgba(7, 17, 27, 0.1);
                li{
                    display: inline-block;
                    margin-right: 0.16rem;
                    color:rgb(77,85,93);
                    background-color: rgb(0,160,220);
                    border-radius: 0.04rem;
                    text-align: center;                   
                                 
                    span{
                        padding: 0.16rem 0.24rem;
                        display: inline-block;
                        font-size: 0.24rem;
                    }
                    span:first-child{
                        padding-right: 0;
                    }
                    span:nth-child(2){
                        font-size: 0.16rem;
                        padding-left: 0;
                    }
                }
                li:first-child{
                    color:#fff;
                }
                li:nth-child(2){
                    background-color:rgba(0,160,220,0.2);
                }
                li:nth-child(3){
                    background-color:rgba(77,85,93,0.2);
                }
            }
        }
        .only{
            padding: 0.24rem 0;            
            font-size: 0rem ; 
            border-bottom: 2px solid #e6e7e8;
            line-height: 0.48rem;
            i{
                display: inline-block;
                font-size: 0.48rem;
                color:rgb(147,153,159);
                line-height: 0.48rem;
                height: 0.48rem;
                margin-left: 0.36rem;
                margin-right: 0.08rem;
                position: relative;
                top:0.1rem;            
                &._blue{
                    color:rgb(0,160,220);
                }
            }
            span{
                font-size: 0.24rem;
                color: rgb(147,153,129);
                display: inline-block;
                line-height: 0.48rem;
                height: 0.48rem;
                padding: 0;              
            }
        }
            
    }
</style>


