import types from '../types.js';
import axios from 'axios';
var state ={
    seller:{},
    detailShow:false
};
var getters={
    seller(state){
        return state.seller;
    },
    detailShow(state){
        return state.detailShow;
    }
};
var actions={
    getSeller({ commit ,state }){
        axios.get('./data.json').then( (res)=>{
            // console.log(res.data)
            // if(res.data.error === 0){
            // console.log(res.data)            
                commit(types.GET_SELLER , res.data.seller);
            // }
        } )
    },
    showDetail({commit}){
        commit(types.SHOW_DETAIL)
    },
    hideDetail({commit}){
        commit(types.HIDEDETAIL)
    }
};
var mutations={
    [types.GET_SELLER](state,data){
        state.seller = data;    
        
    },
    [types.SHOW_DETAIL](state){
        state.detailShow = true;   
    },
    [types.HIDEDETAIL](state){
        state.detailShow = false;
    }
};
export default {
    state,
    getters,
    actions,
    mutations
}



