<template>
  <div class="fooddetail" v-show="detailshow" ref="detail">
    <div>
        <div class="showimg">        
            <img :src="food.image" @click="hidefooddtail">
        </div>
        <div class="foodlist">
            <p>{{food.name}}</p>
            <p>{{food.description}}</p>
            <p>月销量{{food.sellCount}}份<span>好评率{{food.rating}}%</span> </p>    
            <p>
                <span><span style="font-size:0.2rem">￥</span>{{food.price}}</span>
                <span class="old" v-if="food.oldPrice">￥{{food.oldPrice}}</span>  
            </p>
            <div class="joincart" @click="joinin"  v-show="!food.count" >
                <p>加入购物车</p>
            </div>
            <div class="cart" v-show="food.count" >
                <cartcontrol :food='food'></cartcontrol>                
            </div>        
        </div>
        <div class="sliceline"></div>
        <div class="foodintr">
            <span>商品介绍</span>
            <p>
                {{food.info}}
            </p>
        </div>
        <div class="sliceline"></div> 
            <div class="foodrating">
                <span>商品评价</span>
                <div class="ratinglist">
                    <router-link to="/goods/all">
                        <span>全部</span>
                        <span v-if="food.ratings">{{food.ratings.length}}</span>   
                    </router-link>                                        
                    <router-link to="/goods/like">
                        <span>推荐</span>
                        <span>{{likenumber}}</span>    
                    </router-link>                              
                    <router-link to="/goods/dis">
                        <span>吐槽</span>
                        <span>{{disnumber}}</span>  
                    </router-link>
                    
                </div>                    
            </div>
        <div class="duigou">
            <i class="iconfont icon-duigou"></i>
            <span>只看有内容的评价</span>
        </div>
        <keep-alive>
            <router-view></router-view>
        </keep-alive>       
       
    </div>   
  </div>
</template>
<script>
    import { mapGetters , mapActions } from 'vuex';
    import BScroll from 'better-scroll';
    import cartcontrol from '../cartcontrol/cartcontrol.vue';
    import Vue from 'vue';
    
    export default {
        data(){
            return {
                showcart:false
            }
        },
        created(){
            this.showcart=this.food.count;
        },
        computed:{
           ...mapGetters([
            'food',
            'detailshow'
           ]),
            likenumber(){
               let a=0;
               if(this.food.ratings){
                   for( let i = 0 ; i < this.food.ratings.length ; i++){
                   if(!this.food.ratings[i].rateType){
                       a++;                      
                   }                   
               }
                return a;
               }
           },
           disnumber(){
               let b=0;
               if(this.food.ratings){
                   for( let i = 0 ; i < this.food.ratings.length ; i++){
                   if(this.food.ratings[i].rateType){
                       b++;                      
                   }                   
               }
                return b;
               }
           }
        },
         components:{            
            cartcontrol           
        },
        
        methods:{
             ...mapActions([
                'hidefooddtail'
            ]),
            joinin(){ 
                 Vue.set(this.food,'count',1);           
            }
          
        },   
        filters:{
           "data-format":function(data){  
                var da = new Date(data);           
                var year = da.getFullYear();
                var month = da.getMonth()+1;
                var date = da.getDate();
                var hour = da.getHours();
                var minutes = da.getSeconds();                                                 
                return year+"-"+(month<10?"0"+month:month)+"-"+date+" "+(hour<10?"0"+hour:hour)+":"+(minutes<10?"0"+minutes:minutes);
            }
        },
        // created(){			
        //     this.$nextTick( () => {
        //         setTimeout( () => {
        //             this.detailBS();                   
        //         },100);
        //     })
        // }
        watch:{
            food(){        
                this.$nextTick( () => {
                    setTimeout( () => {
                        if( !this.detailBScroll ){                     
                            this.detailBScroll = new BScroll( this.$refs.detail , {
                                click : true
                            });
                        }else{
                            this.detailBScroll.refresh();                  
                        }
                    },30)
                })
            }
        },    
    }
</script>
<style scoped lang="less">
    .fooddetail{
        position: fixed;
        top:0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color:#fff;
        .showimg{
            position: relative;       
            width: 100%;
            height: 0;
            padding-top: 100%;
            img{
                position: absolute;
                top:0;
                left: 0;
                width:100%;
                height: 100%;
            }
        }
        .foodlist{
            padding:0.36rem;
            // width: 100%;
            position: relative;
            .cart{
                position: absolute;
                bottom: 0.36rem;
                right: 0.36rem;
            }
            
            p{
                margin-bottom: 0.16rem;
                color:rgb(147,153,159);
                font-size: 0.2rem;                     
                line-height: 0.2rem;
            }
            p:first-child{
                font-size: 0.28rem;
                color: rgb(7,17,27);
                line-height: 0.28rem;
            }
            p:nth-child(2){
                font-size: 0.2rem;                     
                line-height: 0.2rem;
            }
            p:nth-child(3){
                span{
                    margin-left: 0.24rem;
                }
            }
            p:nth-child(4){   
                overflow: hidden;   
                margin: 0;                                 
                span:first-child{
                    font-size: 0.28rem;                     
                    line-height: 0.2rem;
                    font-weight: 700;
                    line-height: 0.28rem;
                    color: red;
                }
                span:last-child{
                    font-weight: 700;
                    font-weight: 0.2rem;
                    margin-left: 0.16rem;                            
                }
                .old{
                    text-decoration: line-through
                }
            }
            .joincart{
                position: absolute;
                bottom: 0.36rem;
                right: 0.36rem;
                width: 1.48rem;
                background-color: rgb(0,160,220);
                border-radius: 0.24rem;
                text-align: center;
                height: 0.48rem;
                p{
                    color: rgb(255,255,255);
                    line-height: 0.48rem;
                    height: 0.48rem;
                    font-size: 0.2rem;                   
                    margin: 0;
                }
                
            }
        }
        .sliceline{
            padding-top: 0.32rem;
            background-color: #f3f5f7;
            border-top: 1px solid rgba(7, 17, 27, 0.1);
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);            
        }
        .foodintr{
            padding:0.36rem;
            span{
                display: block;
                margin-bottom: 0.12rem;  
                font-size: 0.28rem;            
            }
            p{
                padding:0 0.16rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(77,85,93);
                line-height: 0.48rem;
            }
        }
        .foodrating{
            padding: 0.36rem 0.36rem 0 0.36rem;           
            span{
                display: block;
                margin-bottom: 0.12rem;  
                font-size: 0.28rem;                            
            }
            .ratinglist{
                padding-top:0.24rem;
                padding-bottom: 0.36rem;
                border-bottom:1px solid rgba(7, 17, 27, 0.1);
                a{
                    display: inline-block;
                    margin-right: 0.16rem;
                    padding: 0.16rem 0.24rem;
                    background-color: rgb(0,160,220);
                    border-radius: 0.04rem;
                    text-align: center;                    
                    color:rgb(77,85,93);
                    &.router-link-active{
                        color:#fff;                
                   
                    }
                    span{
                        display: inline;
                        font-size: 0.24rem;
                        
                    }
                    span:nth-child(2){
                        font-size: 0.16rem;
                    }
                }
                a:nth-child(2){
                    background-color:rgba(0,160,220,0.2);
                }
                a:nth-child(3){
                    background-color:rgba(77,85,93,0.2);
                }

            }
            
        }
        .duigou{
            padding: 0.24rem 0;            
            font-size: 0rem ;
            border-bottom: 2px solid #e6e7e8;
            line-height: 0.48rem;
            i{
                display: inline-block;
                font-size: 0.48rem;
                color:rgb(147,153,159);
                line-height: 0.48rem;
                height: 0.48rem;
                margin-left: 0.36rem;
                margin-right: 0.08rem;
                position: relative;
                top:0.1rem;
            }
            span{
                font-size: 0.24rem;
                color: rgb(147,153,129);
                display: inline-block;
                line-height: 0.48rem;
                height: 0.48rem;
                padding: 0;              
            }
        }
        
    }
</style>





