import types from '../types.js';
import axios from 'axios';

var state ={
   goods:[], 
   detailshow:false,
   food:{},
   cartshow:false
};
var getters={
    goods(state){
        return state.goods;
    },
    detailshow(state){
        return state.detailshow;
    },
    food(state){
        return state.food;
    },
    cartshow(state){
        return state.cartshow;
    }
};
var actions={
    getGoods({ commit ,state }){
        axios.get('./data.json').then( (res)=>{
            // console.log(res.data)
            // if(res.data.error === 0){
            // console.log(res.data)            
                commit(types.GET_COODS, res.data.goods);
            // }
        } )
    },
    showfood( { commit , state },food ){
        commit( types.SHOW_FOOD ,  food )
    },
    hidefooddtail( { commit }){
        commit( types.HIDEFOODDTAIL )
    },
    showcartlist({commit} ){
        commit(types.SHOWCARTLIST)
    },
    hidecart({commit}){
        commit(types.HIDECART)
    }

};
var mutations={
    [types.GET_COODS](state,data){
        state.goods = data;   
        //  console.log(state.goods)        
    },
    [types.SHOW_FOOD](state,food){
        state.detailshow = true;
        state.food = food;
        // console.log( state.food)
    },
    [types.HIDEFOODDTAIL](state){
        state.detailshow = false;     
    },
    [types.SHOWCARTLIST](state){
        state.cartshow = true;
        // console.log( state.cartshow )
    },
    [types.HIDECART](state){
        state.cartshow = false;
    }
};
export default {
    state,
    getters,
    actions,
    mutations
}



