<template>
  <div class="header">
      <img :src="seller.avatar" alt="图片丢失">
      <div class="bg_gray"></div>
      <div class='container'>
        <div class="toplist">
            <div class="imgdiv">
                <img :src="seller.avatar" alt="图片丢失" @click="showDetail">
            </div>
            <div class="top_right">
                <ul>
                    <li>
                        <img src="../../assets/imgs/brand.png" alt="">
                        <span>{{seller.name}}</span>
                    </li>
                    <li >
                        <span>{{seller.description}}/</span><span>{{seller.deliveryTime}}</span><span>分钟送达</span>
                    </li>
                    <li v-if="seller.supports">
                        <img src="../../assets/imgs/decrease.png" alt="">
                        <span>{{seller.supports[0].description}}</span>
                        <p class="numbers"  @click="showDetail">
                            <span>{{seller.supports.length}}个</span>
                            <i class="iconfont icon-jiantouyou"></i>
                        </p>
                    </li>
                </ul>
            </div>
        </div> 
        <div class="tips"  @click="showDetail">
             <img src="./bulletin.png" alt="">
             <span>{{seller.bulletin}}</span>           
        </div>
        <i class="iconfont icon-jiantouyou" id="jiantou"></i>        
      </div>      
  </div>
</template>
<script>
    import { mapGetters , mapActions} from 'vuex'
    export default {
        computed:{
            ...mapGetters([
                'seller'
            ])
        },
        methods:{
            ...mapActions([
                'showDetail'
            ])
        }
    }
</script>
<style scoped lang="less">
    .header{
        width: 100%;
        height: 2.68rem;
        overflow: hidden;
		p{
			color:red;
			font-size: 0.32rem;
		}
    }
    .header > img{
        margin-top: 0.2rem;
        height: 100%;
        width: 100%;
        filter: blur(0.1rem);
    }
    .bg_gray{
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        height: 2.68rem;
        width: 100%;     
        background:rgba(7,17,24,0.5);
    }
    .container{
        position: absolute;
        top: 0;       
        height: 2.68rem;
        width: 100%;
    }
    .toplist{
        padding: 0.48rem 0.24rem 0.36rem 0.48rem;
        display: flex;
    }
    .imgdiv{
        height: 1.28rem;
        width: 1.28rem;
        margin-right: 0.32rem; 
    }
    .imgdiv > img{
        height: 100%;
        width: 100%;
        border-radius: 0.04rem;     
        display: block;
    }
    .top_right{ 
        padding:0.04rem 0; 
        color: rgb(255, 255, 255);       
    }
    .top_right li:first-child{
        font-size: 0.32rem;
        font-weight: bold;
        line-height: 0.36rem;
        margin-bottom: 0.16rem;
        img{           
            height: 0.36rem;
            display: inline-block;
            vertical-align: middle;
            margin-right: 0.12rem;
            border-radius: 0.04rem;
            
        }
        span{
            position: relative;
            top:0.02rem;
        }
    }   
    .top_right li:nth-child(2){
        font-size: 0.24rem;
        font-weight: 200;
        line-height: 0.24rem;
        margin-bottom: 0.14rem;
    }
    .top_right li:nth-child(3){
        font-size: 0.20rem;
        font-weight: 200;
        line-height: 0.24rem;
        img{
            height: 0.3rem;         
            vertical-align: middle;
            margin-right: 0.08rem;
            position: relative;
            top:-0.02rem;    
            border-radius: 0.04rem;
                    
        }       
    }
    .tips{
        padding: 0 0.24rem;
        font-size: 0.2rem;
        color:rgb(255,255,255);
        font-weight: 200;
        line-height: 0.56rem;
        height: 0.56rem;
        background-color: rgba(7,17,27,0.2);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space:nowrap;
        span{
           padding: 0 0.08rem;
        }
        img{
           height: 0.3rem;
           vertical-align: middle;
           border-radius: 0.04rem;
           position: relative;
           top:-0.02rem;
        }
      
    }
    .numbers{      
       color:rgb(255,255,255);        
       background-color: rgba(0,0,0,0.2);
       padding: 0.14rem 0.16rem;
       width: 0.8rem;
       text-align: center;
       border-radius: 0.6rem;
       position: absolute;
       top:1.4rem;
       right: 0.24rem;
       i{
          font-size: 0.16rem;
          color:rgb(255,255,255); 
          font-weight: 200;
          height: 0.24rem; 
          line-height: 0.24rem;
          position: relative;
          top:-0.02rem;
       }
       span{
           font-size: 0.2rem;
           color:rgb(255,255,255);
           font-weight: 200;      
           padding-left: 0.04rem;
           line-height: 0.24rem;
       }
       
    }
    #jiantou{  
        font-size: 0.16rem;              
         position: absolute;
         top:2.34rem;
         right:0.22rem;
         color: rgb(255,255,255);           
    }
 


</style>


  


