import types from '../types.js';
import axios from 'axios';
var state ={
    ratings:[],
    types:["满意","不满意","全部"],
    selectRatings:[],
    selectType:2,
    logchange:false,    
};
var getters={
    ratings(state){
        return state.ratings;
    },
    types(state){
        return state.types;
    },
    selectRatings(state){
        return state.selectRatings
    },
    selectType(state){
        return state.selectType
    },
    logchange(state){
        return state.logchange
    }
};
var actions={
    getRatings({ commit ,state }){
        axios.get('./data.json').then( (res)=>{    
            // if(res.data.error === 0){            
                commit(types.GET_RATINGS , res.data.ratings);
            // }
        })
    },
    changeTypes( { commit } , data ){
        commit( types.CHANGE_TYPES , data )       
    },
    select({ commit } , typenumber){
        commit( types.CHOOSE_COUNTLIST , typenumber ) 
    },
    toggle({ commit }){
        commit( types.SELECT_ONLY );
    }
    
};
var mutations={
    [types.GET_RATINGS](state,data){
        state.ratings = data;            
    },
    [types.CHANGE_TYPES](state,data){
        state.types = data;
        state.selectRatings = state.ratings;
    },
    [types.CHOOSE_COUNTLIST](state,number){
        state.selectType = number;
        //filter 将ratings数组中的 元素 按照 0或者1或者2 进行分类，最后得到想后到新的数组，
        state.selectRatings = state.ratings.filter( rating => {
            if( state.selectType === 2 ){
                return true;
            }else{
                return rating.rateType === state.selectType
            }
        });     
    },
    [types.SELECT_ONLY]( state ){
        state.logchange = !state.logchange;
        state.selectRatings = state.ratings.filter( rating => {
            if( state.logchange  &&  !rating.text ){
                return false;
            }else if(state.selectType === 2){
                return true;
            }else{
                return rating.rateType === state.selectType
            }
        });  
        console.log(state.selectRatings) 
    }
    
};
export default {
    state,
    getters,
    actions,
    mutations
}



