import Vue from 'vue'
import Router from 'vue-router'
import Goods from '../components/goods/goods.vue'
import Seller from '../components/seller/seller.vue'
import Ratings from '../components/ratings/ratings.vue'
import Goodsall from '../components/goodsall/goodsall.vue'
import Goodslike from '../components/goodslike/goodslike.vue'
import Goodsdis from '../components/goodsdis/goodsdis.vue'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/goods',
      name:'goods',
      component:Goods,
      children:[
        {path:"all" ,component:Goodsall},
        {path:"like" ,component:Goodslike},
        {path:"dis" ,component:Goodsdis},
        {
          path:"/",         
          redirect:'/goods/all'
        }       
      ]
    },
    {
      path:'/ratings',
      name:'ratings',
      component:Ratings
    },
    {
      path:'/seller',
      name:'seller',
      component:Seller
    },
    {
      path:"/",
      redirect:'/goods'
    }
    

  
  ]
})



