<template>
  <div class="goodsall">     
      <div class="fooderratings">
        <ul v-for="(rating,key) in food.ratings" :key="key" v-if="rating.rateType">
            <li >
                <span>{{rating.rateTime | data-format }}</span>                   
                <p>
                    <span>{{rating.username}}</span>
                    <img :src="rating.avatar" alt="">
                </p>
            </li>
            <li >    
                <i :class="rating.rateType ? 'iconfont icon-zan2' : 'iconfont icon-dianzan_active' "></i>
                <span>{{rating.text}}</span>
            </li>
        </ul>
    </div> 
  </div>
</template>
<script>
    import { mapGetters , mapActions } from 'vuex';
    export default {
        computed:{
           ...mapGetters([
            'food',          
           ])
        },
         filters:{
           "data-format":function(data){  
                var da = new Date(data);           
                var year = da.getFullYear();
                var month = da.getMonth()+1;
                var date = da.getDate();
                var hour = da.getHours();
                var minutes = da.getSeconds();                                                 
                return year+"-"+(month<10?"0"+month:month)+"-"+date+" "+(hour<10?"0"+hour:hour)+":"+(minutes<10?"0"+minutes:minutes);
            }
        },
    }
</script>
<style scoped lang="less">
    .goodsall{
        // color:red;
        // position: absolute;
        .fooderratings{
            padding: 0 0.36rem 0.36rem 0.36rem;
            ul{
                padding: 0.32rem 0;
                border-bottom: 2px solid #e6e7e8;
                li:first-child{
                    font-size: 0.2rem;
                    display: flex;
                    justify-content: space-between;
                    p{
                        font-size: 0;
                        span{
                            font-size: 0.2rem;
                            color:rgb(147,153,159);
                            line-height: 0.24rem;
                            margin-right: 0.12rem;
                        }
                        img{
                            display: inline-block;
                            vertical-align: top;
                            width: 0.24rem;
                            height: 0.24rem;
                            border-radius: 0.12rem;

                        }
                    }                   
                }  
                li:last-child{
                    font-size: 0;
                    i{                   
                        color: rgb(147,153,159);
                        line-height: 0.48rem;
                        margin-right: 0.08rem;
                        &.icon-dianzan_active{
                            color:rgb(0,160,220);
                        }
                         &.icon-zan2{
                             font-size: 0.24rem;
                         }
                    }
                    span{
                        font-size: 0.24rem;
                        color: rgb(7,17,27);
                        line-height: 0.32rem;
                    }
                }              
            }
            ul:last-child{
                border:0;
                margin-bottom: 1rem;
            }
        }
    }
</style>





