<template>
  <div class="seller" ref="sellerbs">
	<div>
		<div class="sellerTop">
			<div class="sellerinfo">
				<div>
					<span>{{seller.name}}</span> 
					<i class="iconfont icon-aixin"></i>
				</div>
				<div>
					<p>
						<star :score = 'seller.score'   class="star"></star>
						<span style="margin-left:0.16rem;" >({{seller.ratingCount}})</span>
						<span style="margin-left:0.24rem;">月售{{seller.sellCount}}单</span>
					</p>
					<span>已收藏</span>
				</div>
				</div>
			
		</div>
		<div class="sellerlist">
			<ul>
				<li>
					<p>起送价</p>
					<span>{{seller.minPrice}}</span><span>元</span>
				</li>
				<li>
					<p>
						<span v-if="!seller.description">商家配送</span>
						<span v-if="seller.description">{{seller.description}}</span>
					</p>
					<span>{{seller.deliveryTime}}</span><span>元</span>
				</li>
				<li>
					<p>平均配送时间</p>
					<span>{{seller.deliveryTime}}</span><span>分钟</span>
				</li>
			</ul>
		</div>
		<div class="sliceline"></div> 
		<div class="notice">
			<p>公告与活动</p>
			<p>{{seller.bulletin}}</p>
		</div>	
		<div class="info">
			<ul>
				<li v-if="seller.supports">
					<img src="./decrease.png" alt="">
					<span>{{seller.supports[0].description}}</span>
				</li>
					<li v-if="seller.supports">
					<img src="./discount.png" alt="">
					<span>{{seller.supports[1].description}}</span>
				</li>
					<li v-if="seller.supports">
					<img src="./discount.png" alt="">
					<span>{{seller.supports[2].description}}</span>
				</li>
					<li v-if="seller.supports">
					<img src="./invoice.png" alt="">
					<span>{{seller.supports[3].description}}</span>
				</li>
					<li v-if="seller.supports">
					<img src="./special.png" alt="">
					<span>{{seller.supports[4].description}}</span>
				</li>
			</ul>
		</div>
		<div class="sliceline"></div> 
		<div class="sellerpic">
			商家实景
		</div>
		<div class="pic-wrap" ref='menuWrap'>
			<ul class="" ref='picList'>
				<li v-for='(item,i) in seller.pics' class="pic-item" :key="i">
					<img :src="item" >
				</li>
			</ul>
		</div>
		<div class="sliceline"></div> 
		<div class="infos">
			<p>商家信息</p>
			<ul>
				<li v-for="(infoitem ,key) in seller.infos " :key="key">
					{{infoitem}}
				</li>
			</ul>
		</div>
	</div>

  </div>
</template>
<script>
    import BScroll from 'better-scroll';   
	import { mapGetters , mapActions} from 'vuex';   
	import star from '../star/star.vue';
    export default {
        computed:{
            ...mapGetters([
				'seller', 
            ]),  
            
		},
		components:{
			star,
		},
		methods:{
			sellerBS(){
				if(this.seller){
					this.$nextTick( () => {
                        if( !this.sellerBScroll ){                     
                            this.sellerBScroll = new BScroll( this.$refs.sellerbs , {
                                click : true
							});	
							console.log(this.sellerBScroll)						
                        }else{
                            this.sellerBScroll.refresh();                  
                        }
                 	 })
				}
			},
			picBS(){
				let picWidth = 120;
	          	let margin = 6;
	          	let width = (picWidth + margin) * this.seller.pics.length - margin;
	          	this.$refs.picList.style.width = width + 'px';
				this.$nextTick( ()=>{
					if( !this.picBScroll ){
	 				this.picBScroll = new BScroll( this.$refs.menuWrap , {
							scrollX: true,
						})
					}else{
						this.picBScroll.refresh();
					}
				});
 				
			}
		},
		created(){
			setTimeout(()=> {
				this.sellerBS();
				this.picBS();
			}, 300);
		},
	}
 
</script>
<style scoped lang="less" >
    .seller{
		height: 9rem;
		overflow: hidden;
		.sellerTop{
			padding: 0 0.36rem;
			.sellerinfo{
				padding: 0.36rem 0;
				border-bottom: 1px solid rgba(7, 17, 27, 0.1);
				div{
					display: flex;
					justify-content: space-between;
				}
				div:first-child{
					span{
						font-weight: 600;
						font-size: 0.28rem;
						color: rgb(7, 17, 27);
						line-height: 0.28rem;
						padding-top: 0.08rem;
					}
					i{
						font-size: 0.48rem;
						color: rgb(240,20,20);
						line-height: 0.48rem;
					}
				}
				div:nth-child(2){
					p{
						font-size: 0;
						.star{
							display: inline-block;
						}
						span{
							font-size: 0.2rem;
							color:rgb(77,85,93);
							display: inline-block;
							line-height: 0.31rem;
							height: 0.31rem;
							vertical-align: top;
						}
						
					}
					span{
						display: inline-block;
						padding-top: 0.08rem;
						font-size: 0.2rem;
						color: rgb(77,85,93);
						line-height: 0.2rem;
					}
				}
			}
			
		}
		.sellerlist{
			ul{
				margin: 0.36rem;
				display: flex;
				li{
					flex:1;
					border-right: 1px solid rgba(7, 17, 27, 0.1);
					text-align: center;
					p{
						font-size: 0.2rem;
						color: rgb(147,153,159);
						margin-bottom: 0.08rem;
					}
					span:nth-child(2){
						font-size: 0.48rem;
						font-weight: 200;
						color: rgb(7, 17, 27);
						line-height: 0.48rem;
					}
					span:nth-child(3){
						font-size: 0.2rem;
					}
				}
				li:last-child{
					border:0;
				}
			}
		}
		.sliceline{
			width:100%;
            padding-top: 0.32rem;
            background-color: #f3f5f7;
            border-top: 1px solid rgba(7, 17, 27, 0.1);
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);            
		}
		.notice{
			padding: 0 0.36rem;
			p:first-child{
				margin-top: 0.36rem;
				font-size: 0.28rem;
				color: rgb(7, 17, 27);
				line-height: 0.28rem;
				margin-bottom: 0.16rem;
				font-weight: 600;
			}
			p:nth-child(2){
				padding: 0 0.24rem;
				font-size: 0.24rem;
				font-weight: 200;
				color:rgb(240,20,20);
				line-height: 0.48rem;
				border-bottom: 1px solid rgba(7, 17, 27, 0.1);
				padding-bottom: 0.32rem;
			}
		}
		.info{
			padding: 0 0.36rem;
			li{
				color:#000;
				padding: 0.32rem 0.24rem;
				border-bottom: 1px solid rgba(7, 17, 27, 0.1);
				font-size: 0;
				img{
					height: 0.32rem;
					border-radius: 0.04rem;
					line-height: 0.32rem;  
					margin-right: 0.12rem;   
				}
				span{
					color: rgb(7, 17, 27);
					font-size: 0.24rem;
					font-weight: 200;
					display: inline-block;
					line-height: 0.32rem;
					vertical-align: top;
				}

			}
			li:last-child{
				border:0;
			}
		}
		.sellerpic{
			margin:0.36rem 0 0.24rem 0.36rem;
			font-size: 0.28rem;
			color: rgb(7, 17, 27);
			line-height: 0.28rem;	
			font-weight: 600;	
		}
		.infos{
			padding: 0.36rem;
			p{
				font-weight: 600;	
				font-size: 0.28rem;
				color: rgb(7, 17, 27);
				line-height: 0.28rem;
				padding-bottom: 0.24rem;
				border-bottom: 1px solid rgba(7, 17, 27, 0.1);
			}
			li{
				padding: 0.32rem 0.24rem;
				font-size: 0.24rem;
				font-weight: 200;
				color: rgb(7,17,27);
				line-height: 0.32rem;
				border-bottom: 1px solid rgba(7, 17, 27, 0.1);
			}
			li:last-child{
				border:0;
			}
		}
		
		.pic-wrap{
			margin-left: 0.36rem;
			width: 100%;
			overflow: hidden;
			box-sizing: border-box;
			white-space: nowrap;
			.pic-item{
				display: inline-block;
				margin-right: 0.06rem;
				margin-bottom: 0.36rem;
				width: 2.4rem;
				height: 1.8rem;
				img{
					width: 100%;
					height: 100%;
				}
				&:last-child{
					margin: 0;
				}
			}
		}
	}
</style>


