<template>
  <div class="shopcart"  >
      <div class="gray" @click="hidecart"></div>
      <div class="showlist">
          <div class="cartclear">
            <p>购物车</p>
            <p @click="deleteAll">清空</p>
          </div>
          <div  v-show="!selectFoods.length" class="noneTip">
              <p>您还没有选中任何商品哦!</p>
          </div>
          <div class="lists" v-if="selectFoods" ref="cartlist">
              <!-- {{selectFoods}} -->
              <div >
                    <ul>
                        <li  v-for="(food,i) in selectFoods" :key="i" >
                            <span style=" width: 4rem;">{{food.name}}</span>
                            <span><span>￥</span>{{food.price}}</span>
                            <div class="cartnumber">
                                <cartcontrol :food='food'></cartcontrol>
                            </div> 
                        </li>
                    </ul>
              </div>
          </div>
      </div>
  </div>
</template>
<script>
    import BScroll from 'better-scroll';
    import { mapGetters,mapActions,mapState } from 'vuex';
    import cartcontrol from '../cartcontrol/cartcontrol.vue';
    export default {
        props:['selectFoods'],
        computed:{
              ...mapGetters([
                'cartshow',                                 
            ]),
        }, 
        components:{            
            cartcontrol,                     
        },
        watch:{
            cartshow(){        
                this.$nextTick( () => {
                    setTimeout( () => {
                        if( !this.cartScroll ){                     
                            this.cartScroll = new BScroll( this.$refs.cartlist , {
                                click : true
                            });
                        }else{
                            this.cartScroll.refresh();                  
                        }
                    },30)
                })
            }
        },
        methods:{          
            ...mapActions([
                'hidecart'
            ]),
            deleteAll(){
                this.selectFoods.forEach((food)=>{
                    food.count=0
                });
            }
        },      
  
    }
</script>
<style scoped lang="less" >
    .shopcart{       
        position: fixed;   
        bottom:0.9rem;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: -1;
        .gray{
            position: absolute;
            height: 100%;
            width: 100%;
            background-color:rgba(7,17,27,0.6);
            filter: blur(0.1rem);
        } 
        .showlist{
            position: absolute;    
            bottom:0;            
            height: 6.11rem;
            width: 100%;
            background-color: white;  
            .cartclear{
                height: 0.8rem;
                background-color: #f3f5f7;
                padding: 0 0.36rem;
                display: flex;
                justify-content: space-between;
                border-bottom:1px solid rgba(7,17,27,0.1);
                p{
                    line-height: 0.8rem;
                    height: 0.8rem;
                }
                p:first-child{
                    color:rgb(7,17,27);
                    font-weight: 200;
                    font-size: 0.28rem;
                }
                p:last-child{
                    color:rgb(0,160,220);
                    font-weight: 200;
                    font-size: 0.24rem;
                }
            }
            .noneTip{
                text-align: center;
                margin-top: 2rem;
            }
            .lists{
                overflow: hidden;
                height: 5rem;
                padding: 0 0.36rem;
                li{
                    padding: 0.24rem 0;
                    height: 0.479rem;
                  
                    border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                    line-height: 0.479rem;
                    overflow: hidden;
                    span{
                        font-size: 0.28rem;
                        display: inline-block;
                        color:rgb(7,17,27);                       
                    }
                    span:nth-child(2){
                        color:rgb(240,20,20);
                        font-weight: 700;
                        margin: 0 0.24rem 0 0.36rem;
                        span{
                            font-size: 0.2rem;
                            color:rgb(240,20,20);
                            font-weight: normal;
                        }
                    }
                    .cartnumber{
                       float: right;
                       i,span{
                           display: inline-block;
                       }

                    }
                }
                li:last-child{
                    border:0;
                }
            }
        }       
    }
</style>





