<template>
  <div class="goods">
      <div class="goods-menu" ref="menu">
          <ul>
            <li href="#" v-for="(nameitem , key) in goods" :key="key" class="menu-item" 
            @click="selectMenu(key)" :class="{'current': key===currentIndex }" >   
                <span>{{nameitem.name}}</span>
            </li>   
          </ul>       
      </div>
      <div class="goods-view" ref="rightlist">
        <div>
            <div v-for="(nameitem , key) in goods" :key="key" class="food-list-hook" > 
                <div class="title" >
                    <span >{{nameitem.name}}</span>
                </div>
                <div class="goodslist" v-for="(details , key ) in  nameitem.foods" :key="key">
                    <img :src="details.image"  @click="showfood(details)">
                    <div class="goodslist_right">
                        <p>{{details.name}}</p>
                        <p>{{details.description}}</p>
                        <p>月销量{{details.sellCount}}份<span>好评率{{details.rating}}%</span> </p>
                        <p>
                            <span><span style="font-size:0.2rem">￥</span>{{details.price}}</span>
                            <span class="old" v-if="details.oldPrice">￥{{details.oldPrice}}</span>                     
                        </p>
                        <div class="cart">
                            <cartcontrol :food='details'></cartcontrol>
                        </div>
                    </div>
                </div>              
            </div>            
        </div>
      </div>  
      <fooddetail></fooddetail>
  </div>  
</template>
<script>
    import BScroll from 'better-scroll';
    import fooddetail from '../fooddetail/fooddetail.vue';
    import cartcontrol from '../cartcontrol/cartcontrol.vue';
    import { mapGetters , mapActions} from 'vuex';    
    export default {
        data(){
            return {
                listHeight : [],
                scrollY:0                              
            }
        },
        components:{
            fooddetail,
            cartcontrol,                      
        },
        computed:{
            ...mapGetters([
                'goods'       
            ]),           
            currentIndex(){
				// 循环listHeight数组
				for( let i = 0 ; i < this.listHeight.length ; i++ ){
					let height1 = this.listHeight[i];		// 0
					let height2 = this.listHeight[i+1];		// 1434
					// 根据滚动的y轴坐标值 与listHeight数组区间进行比较 
					if( this.scrollY >= height1 && this.scrollY < height2 ){
						return i;
					}
				}
				return 0 ;
			}
        },
        methods:{
            menuBS(){
                let menuBScroll = new BScroll(this.$refs.menu, {click :true} );
                this.listBScroll = new BScroll(this.$refs.rightlist, {click :true,probeType:3} );
                this.listBScroll.on('scroll',(pos)=>{
                    // console.log(Math.abs(Math.round(pos.y)))
                    this.scrollY=Math.abs( Math.round( pos.y ) ) + 2;
                })
            },
            calcHeight(){
                let footList = this.$refs.rightlist.getElementsByClassName('food-list-hook');
                let height = 0;
                this.listHeight.push(height);
                for( let i = 0 ; i < footList.length ; i++){                   
                    height += footList[i].clientHeight;
                    this.listHeight.push(height)
                }                
            },
            selectMenu(i){
                let footList = this.$refs.rightlist.getElementsByClassName('food-list-hook');
                let el = footList[i];
                this.listBScroll.scrollToElement( el , 300);
            },
            showfood(food){
                this.$store.dispatch( 'showfood' , food );
            }
        } ,
        created(){			
			this.$nextTick( () => {
				setTimeout( () => {
                    this.menuBS();
                    this.calcHeight();
				},100);
			})
		}
   
    }
</script>
<style scoped lang="less" >
    .goods{
        position: absolute;
        top:3.5rem;
        overflow: hidden;
        display: flex;
        bottom: 0.9rem;
        .goods-menu{
            flex:0 0 1.6rem;
            width: 1.6rem;
            background-color: #f3f5f7;                       
            ul{                                     
                li{   
                    padding: 0 0.24rem;                
                    display: table;              
                    font-size: 0.24rem;
                    font-weight: 200;
                    height: 1.08rem;
                    color: rgb(77,85,93);                      
                    width: 1.12rem;                         
                    &.current{
                        color:#000;
                        background-color: #fff;
                        span{
                            border:0;
                        }
                    }    
                    span{
                        display: table-cell; 
                        vertical-align: middle;
                        border-bottom: 1px solid rgba(7,17,27,0.1);                 
                    }                     
                
                }
                li:nth-child(2){                                       
                    span{
                        border: 0;
                        text-indent: 0.32rem;
                        background: url('./special.png') 0 0.2rem no-repeat;
                        background-size: 0.3rem 0.3rem      
                    }          
                }  
                li:first-child,li:last-child{
                    span{
                        border:0;
                    }
                }
            }

        }
        .goods-view{
            flex: 1;
            .title{
                font-size: 0.24rem;
                color: rgb(147,153,159);
                border-left: 2px solid #d9dde1;
                background-color: #f3f5f7;
                height: 0.52rem;
                padding-left: 0.28rem;
                margin-bottom: 0.26rem;
                span{
                    line-height: 0.52rem;
                    height: 0.52rem;
                }
            }
            .goodslist{
                margin:0 0.36rem ;
                padding:0.36rem 0;
                display: flex;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                img{
                   width:1.14rem;
                   height: 1.14rem;
                   margin-right: 0.26rem;
                }
                .goodslist_right{
                    margin-top:0.04rem;
                    width: 70%;
                    position: relative;
                    .cart{
                        position: absolute;
                        bottom:-0.1rem;
                        right:-0.2rem;
                    }
                    
                    p{
                        margin-bottom: 0.16rem;
                        color:rgb(147,153,159);
                        font-size: 0.2rem;                     
                        line-height: 0.2rem;
                    }
                    p:first-child{
                        font-size: 0.28rem;
                        color: rgb(7,17,27);
                        line-height: 0.28rem;
                    }
                    p:nth-child(2){
                        font-size: 0.2rem;                     
                        line-height: 0.2rem;
                    }
                    p:nth-child(3){
                        span{
                            margin-left: 0.24rem;
                        }
                    }
                    p:nth-child(4){   
                        overflow: hidden;   
                        margin: 0;                                 
                        span:first-child{
                            font-size: 0.28rem;                     
                            line-height: 0.2rem;
                            font-weight: 700;
                            line-height: 0.28rem;
                            color: red;
                        }
                        span:last-child{
                            font-weight: 700;
                            font-weight: 0.2rem;
                            margin-left: 0.16rem;                            
                        }
                        .old{
                            text-decoration: line-through
                        }
                        
                        input{
                            width: 0.48rem;
                            float: right;
                            text-align: center;
                            line-height: 0.48rem;
                            font-size: 0.2rem;
                            outline: 0;
                        }
                    }
                }
                
            }
            .goodslist:last-child{
                border:0;
            }
            
        }
   
        
    }
    
</style>





