<template>
  <div class="cartcontrol">        
       <i  v-show="food.count" class="icon iconfont icon-jian" @click="minus"></i>   
       <span v-show="food.count">{{food.count}}</span>  
       <i class="iconfont icon-anonymous-iconfont-copy" @click="add"></i>              
  </div>
</template>
<script>
    import Vue from 'vue';
    export default {
        props:[
            'food',          
        ],
        methods:{
           add(){
               if(!this.food.count){
                   Vue.set(this.food,'count',1)
               }else{
                   this.food.count++
               }
           },
           minus(){
              if(this.food.count >= 1){
                  this.food.count--;
              }
           }   
        }
    }
</script>
<style scoped lang="less" >
    .cartcontrol{    
        font-size: 0;
        i{
            font-size: 0.48rem;
            color:rgb(0,160,220);
            line-height: 0.48rem;   
       
        }
        span{
            width:0.48rem;
            display: inline-block;
            color: rgb(147,153,159);
            font-size: 0.3rem;
            height: 0.48rem;
            line-height: 0.48rem;
            vertical-align: top;
            text-align: center;
        }
    }
</style>




