<template>
  <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
    <div class="detail" v-show = 'detailShow'>
        <p @click="hideDetail"  > {{seller.name}} </p>
        <star :score = 'seller.score'  :size="'big'" class="star"></star>
        <div class="line">
            <p>
                <span></span>
                &nbsp;优惠信息 &nbsp;
                <span></span>
            </p>
        </div>
        <div class="info">
            <ul>
                <li v-if="seller.supports">
                    <img src="./decrease.png" alt="">
                    <span>{{seller.supports[0].description}}</span>
                </li>
                 <li v-if="seller.supports">
                    <img src="./discount.png" alt="">
                    <span>{{seller.supports[1].description}}</span>
                </li>
                 <li v-if="seller.supports">
                    <img src="./discount.png" alt="">
                    <span>{{seller.supports[2].description}}</span>
                </li>
                 <li v-if="seller.supports">
                    <img src="./invoice.png" alt="">
                    <span>{{seller.supports[3].description}}</span>
                </li>
                 <li v-if="seller.supports">
                    <img src="./special.png" alt="">
                    <span>{{seller.supports[4].description}}</span>
                </li>
            </ul>
        </div>
        <div class="line">
            <p>
                <span></span>
                &nbsp;商家公告 &nbsp;
                <span></span>
            </p>
        </div>
        <div class="infos">
            <ul>
                <li v-for="(infoitem ,key) in seller.infos " :key="key">
                    {{infoitem}}
                </li>
            </ul>
        </div>
        <span class="img_close" @click="hideDetail">
           <img src="./close.png" alt="">  
        </span>
    </div>      
  </transition>
</template>
<script>
    import { animate } from 'animate.css';
    import { mapGetters,mapActions } from 'vuex';
    import star from '../star/star.vue'
    
    export default {
        computed:{
            ...mapGetters([
                'seller',
                'detailShow'
            ])
        },
        methods:{
            ...mapActions([
                'hideDetail'
            ])
        },
        components:{
            star
        },
    }
</script>
<style scoped lang="less">
    .detail{
        position: absolute;
        top:0;        
        padding-top: 1.2rem ;
        width:100%;
        height: 100%;        
        background-color:rgba(7,17,27, 0.8);   
        z-index: 100;       
        p{  
            width: 100%;
            font-size: 0.32rem;
            font-weight: 700;
            color:rgb(255,255,255);
            line-height: 0.32rem; 
            text-align: center;  
            margin-bottom: 0.32rem;      
        }
        .star{
            width: 100%;
            text-align: center;
        }
    }
    .line{
        margin-top: 0.56rem;
        margin-bottom:0.48rem;
        p{
            font-size: 0.28rem;
            span{
                display: inline-block;
                width: 2.24rem;
                height: 0.02rem;
                background-color: rgba(255, 255, 255, 0.2);
                position: relative;
                top:-0.1rem;
            }
        }
    }
    .info{
        padding-left:0.96rem;
        li{
            font-size: 0.24rem;
            font-weight: 200;
            line-height: 0.24rem;
            color: rgb(255,255,255);
            margin-bottom: 0.24rem;           
            img{
                height: 0.32rem;
                border-radius: 0.04rem;
                vertical-align: middle;
                line-height: 0.32rem;  
                margin-right: 0.12rem;   
            }
            span{
                display: inline-block;
                line-height: 0.32rem;
                position: relative;
                top:0.02rem
            }

        }
    }
    .infos{
        padding-left:0.96rem;       
        padding-right:0.96rem;
        li{
            font-size: 0.24rem;
            font-weight: 200;
            line-height: 0.48rem;        
            color: rgb(255,255,255); 
        }
    }
    .img_close{
        height:0.64rem;
        width:0.64rem;
        position: absolute;
        bottom: 1.28rem;
        left:50%;
        margin-bottom:0.64rem;
        margin-left: -0.32rem;

        img{
            width:0.64rem;
             height:0.64rem;            
        }
    }
</style>





